import React, { Component, Fragment } from "react";
import axios from "./axios";

class ActiveVersion extends Component {
  state = {
    data: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");
    axios
      .get("https://api.wordpress.org/stats/plugin/1.0/?slug=" + slug)
      .then((res) => {
        this.setState({ data: res.data });
      });
  }

  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    var d = this.state.data;
    if (!d) return <p className="loading"></p>;

    return (
      <Fragment>
        <table className="table table-striped">
          <thead>
            <tr>
              <th colSpan="2">
                {" "}
                <h6>Active Version</h6>
              </th>
            </tr>
          </thead>
          <tbody>
            {Object.entries(d).map((t, k) => (
              <tr key={k}>
                <td>{t[0]}</td>
                <td>{t[1]}%</td>
              </tr>
            ))}
          </tbody>
        </table>
      </Fragment>
    );
  }
}
export default ActiveVersion;
