import React, { Fragment } from "react";
import axios from "./axios";
import Widget from "./widget";
import Downloads from "./download";
import { icon } from "@fortawesome/fontawesome-svg-core";

export default class PluginStates extends React.Component {
  state = {
    info: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");
    axios
      .get(
        "http://api.wordpress.org/plugins/info/1.0/" +
          slug +
          ".json?fields=banners,icons,active_installs"
      )
      .then((res) => {
        this.setState({ info: res.data });
      });
  }
  /*Sum of (weight * number of reviews at that weight) / total number of reviews
  //(5*252 + 4*124 + 3*40 + 2*29 + 1*33) / 478 = 4.1
*/
  render() {
    const pluginInfo = this.state.info;
    if (!pluginInfo) return <p className="loading"></p>;
    const { ratings } = pluginInfo;
    //console.log(ratings);
    if (!ratings) return <p className="loading"></p>;
    const downloaded = pluginInfo.downloaded;
    const keys = Object.keys(ratings);
    const values = Object.values(ratings);
    const num_ratings = pluginInfo.num_ratings;
    const { icons } = pluginInfo;
    const iconsUrl = icons["1x"];
    const getActiveInstall = pluginInfo.active_installs;

    const str = getActiveInstall.toString(); // Convert to string
    const subsrt = str.substring(1); // subsring of first digit

    const getfirstDigit = str.charAt(0); // get first digit
    const digit = parseInt(getfirstDigit) + 1;
    const probable_active_install = digit + subsrt;

    var AvgRating =
      (keys[0] * values[0] +
        keys[1] * values[1] +
        keys[2] * values[2] +
        keys[3] * values[3] +
        keys[4] * values[4]) /
      num_ratings;
    AvgRating = AvgRating.toFixed(2);

    return (
      <Fragment>
        <div style={{ marginLeft: "260px", marginTop: "30px" }}>
          <div className="container">
            <ul className="list-unstyled mt-5">
              <li>
                <h5>
                  <img
                    src={iconsUrl}
                    style={{ height: "70px", marginRight: "15px" }}
                    alt=""
                  />
                  {pluginInfo.name}
                </h5>
              </li>
            </ul>
            <Widget
              AvgRating={AvgRating}
              downloaded={downloaded}
              num_ratings={pluginInfo.num_ratings}
              active_installs={pluginInfo.active_installs}
              number_of_rating={pluginInfo.num_ratings}
              probable_active_install={probable_active_install}
            />
            <Downloads />
          </div>
        </div>
      </Fragment>
    );
  }
}
