import React, { Fragment } from "react";
import { Redirect, Link } from "react-router-dom";

class AddSlug extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      /*  Initial State */
      msg: "Add a plugin slug",
      className: "alert alert-warning",
      slug: "",
    };
  }

  handleClick = (e) => {
    e.preventDefault();
    console.log(this.state);
    if (this.state.slug !== "") {
      localStorage.setItem("slug", this.state.slug);

      window.location = "/plugin_stats";
      this.setState({
        msg: "Successfully Added.",
        className: "alert alert-success",
      });
    }
  };
  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col-md-6 mt-5 offset-3">
            <div className={this.state.className}>{this.state.msg}</div>

            <input
              type="text"
              className="form-control"
              name="slug"
              placeholder="contact-form-7"
              onChange={this.handleChange}
            />

            <button
              className="btn btn-primary btn-block mt-3"
              onClick={this.handleClick}
            >
              Add Slug{}
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default AddSlug;
