import React, { Component } from "react";
import axios from "./axios";
import ZingChart from "zingchart-react";

class Downloads extends Component {
  state = {
    data: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");
    axios
      .get(
        "http://api.wordpress.org/stats/plugin/1.0/downloads.php?slug=" +
          slug +
          "&limit=60"
      )
      .then((res) => {
        this.setState({ data: res.data });
      });
  }

  constructor(props) {
    super(props);
    this.chartReference = React.createRef();

    this.state = {
      config: {
        title: {
          text: "DOWNLOADS PER DAY",
        },
        type: "line",
        plot: {
          aspect: "spline",
        },
      },
    };
  }

  render() {
    var d = this.state.data;
    if (!d) return <p className="loading"></p>;
    const data = Object.values(d);
    var mySeries = [{ values: data }];

    return (
      <div className="mt-4">
        <ZingChart data={this.state.config} series={mySeries} />
      </div>
    );
  }
}
export default Downloads;
